/*
Custom Modal
*/
.tf-modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999999;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0;
  visibility: hidden;
  opacity: 0;
  -webkit-transform: scale(0.7);
  -ms-transform: scale(0.7);
  -o-transform: scale(0.7);
  transform: scale(0.7);
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.tf-modal.tf-modal-show {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}
.tf-modal .tf-modal-dialog {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  min-height: calc(100% - 40px);
  max-width: 100%;
  margin: 20px auto;
  position: relative;
  pointer-events: none;
}
.tf-modal .tf-modal-content {
  position: relative;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  width: 80%;
  max-width: 1080px;
  pointer-events: auto;
  background-color: #f5f8ff;
  outline: 0;
  margin: 0 auto;
  border-radius: 10px;
  padding: 30px;
  overflow: hidden;
}
.tf-modal .tf-modal-close {
  color: #003162;
  cursor: pointer;
  font-size: 22px;
  position: absolute;
  top: 10px;
  right: 10px;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.tf-modal .tf-modal-close:hover {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  transform: rotate(180deg);
}

/*
Ajax Loader
*/
.tf-btn-loading {
  padding-right: 40px !important;
  position: relative;
}

.tf-btn-loading:before {
  content: "";
  position: absolute;
  top: 50%;
  right: 10px;
  margin-top: -7px;
  display: inline-block;
  width: 12px;
  height: 12px;
  border: 2px solid #ffffff;
  border-left-color: transparent;
  border-radius: 50%;
  vertical-align: middle;
  visibility: visible;
  opacity: 1;
  transition: opacity 0.2s ease, visibility 0.2s ease;
  -webkit-animation: tf-rotate 450ms infinite linear;
  animation: tf-rotate 450ms infinite linear;
}

@-webkit-keyframes tf-rotate {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes tf-rotate {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
body.tf-modal-open {
  overflow: hidden;
}
body.tf-modal-open .tf-modal {
  overflow-x: hidden;
  overflow-y: auto;
}
body.tf-modal-open:after {
  content: "";
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 99999;
  background: rgba(0, 0, 0, 0.4);
}

